.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.hidden{
  display: none;
}
.fade-scale-enter {
  opacity: 0.01;
  /*transform: scale(0);*/
}
.fade-scale-enter.fade-scale-enter-active {
  opacity: 1;
  /*transform: scale(1);*/
  /*ransition: all 1000ms cubic-bezier(0.25, 0.1, 0.25, 1);*/
  transition: opacity 2000ms ease-in;
}
.fade-scale-exit {
  opacity: 1;
  /*transform: scale(1);*/
}
.fade-scale-exit.fade-scale-exit-active {
  opacity: 0.01;
  /*transform: scale(0);
  transition: opacity 1000ms cubic-bezier(0.25, 0.1, 0.25, 1);*/
  transition: opacity 2000ms ease-in;
}
