body {
  margin: 0;
  background-color: black;
  font-family: "poppins" sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#barra {
  justify-content: center;
}
#boton {
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 150px;
  font-family: poppins;
  font-weight: 500;
}
.foto {
  
  border-radius: 150px;
  width: auto;
  border: solid;
  border-color: #17a2b8;
}
.datos strong{
  color: white;
  font-size: 2em;
  font-family: poppins;
}
.datos{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}
p{
  font-family: poppins;
  color: white;
}
ul{
  margin-top: 50px;
  text-align: left;
  list-style: none;
  font-size: 1.1em;
  font-family: poppins;
}
